.button{position:absolute;top:70px;right:14px;height:70px;z-index:101;box-sizing:content-box;user-select:none;background-repeat:no-repeat;background-size:42px 42px;background-image:url("../img/Minesweeper/bomb.png");position:absolute;width:42px;height:42px;background-color:#ed8e33;cursor:pointer;border-radius:30px;border:2px solid rgba(0,0,0,0)}.button > svg{width:24px;height:24px;position:absolute;left:9px;top:9px}.button > svg > :nth-child(2){fill:#fafafa}.button:hover,.button:focus{background-color:#fafafa;border-color:#cc6f15}.button:hover > svg > :nth-child(2),.button:focus > svg > :nth-child(2){fill:#cc6f15}.button.highlite{background-image:url("../img/Minesweeper/flag.png")}